<template>
	<view class="u-card-wrap">
		<u-card @click="navigator(item.id)" v-for="(item,index) in list" :key="index" 
			:title="item.title" :sub-title="item.subTitle"  padding="30" border="true">
			<view class="" slot="body">
				<view class="u-body-item u-flex u-border-bottom u-col-between u-p-t-0">
					<view class="u-body-item-title u-line-2" style="width: 500rpx;">
						{{item.text}}
					</view>
					<image
						:src="item.src"
						mode="aspectFill"></image>
				</view>
			</view>
		</u-card>
	</view>
</template>

<script>
	export default {
		// 接收外界传来的数据
		props: ['list'],
		methods: {
			navigator(id) {
				// 调用父组件的方法
				this.$emit('itemClick', id)
			}
		},
	}
</script>

<style scoped lang="scss">
	.u-demo {
		padding-top: 0;
	}

	.u-card-wrap {
		background-color: $u-bg-color;
		padding: 1px;
	}

	.u-body-item {
		font-size: 32rpx;
		color: #333;
		padding: 20rpx 10rpx;
	}
	
	.u-body-item-title u-line-2 {
		width: 800rpx;
	}
	.u-body-item image {
		width: 120rpx;
		flex: 0 0 120rpx;
		height: 120rpx;
		border-radius: 8rpx;
		margin-left: 100rpx;
		margin-top: 10px;
	}
</style>
