<template>
	<view>
		
		<view>
			<cu-custom bgColor="bg-gradual-blue" :isBack="true">
				<block slot="backText">返回</block>
				<block slot="content">存档页面</block>
			</cu-custom>
		</view>
		
		<view>
			<uploadItem @itemClick="goDetail" :list="list"></uploadItem>
		</view>
		
		<view class="padding flex flex-wrap justify-between align-center bg-white">
			<button class="cu-btn round" @click="myCliack1">提交</button>
		</view>
		
		<view class="padding flex flex-wrap justify-between align-center bg-white">
			<button class="cu-btn round" @click="myCliack2">拍照</button>
		</view>

	</view>
</template>

<script>
	import uploadItem from '../../components/uploadItem/index.vue';
	export default {
		data() {
			return {
				list: [{
					// 编号
					id: '1',
					// 头部标题
					title: '验证码:'+ '1234 5678 9000',
					// 头部时间
					subTitle: '2020-05-15',
					// 图片
					src: 'https://cdn.jsdelivr.net/gh/Dorian1015/cdn/img/custom/tuxiang.jpg',
					// 图片信息
					text: '合同章',
				
				}, ]

			}
		},
		onUnload() {
			uni.switchTab({
				url: '../index/index'
			})
		},
		// 注册组件
		components: {
			"uploadItem": uploadItem
		},
		methods: {


			// 点击跳转
			goDetail(id) {
				console.log("id：" + id)
				uni.navigateTo({
					url: '/pages/upload/edit?id=' + id
				})
			},

			// 点亮下一步 
			myCliack1() {
				//当前页
				var pages = getCurrentPages();
				//上个页面
				var beforePage = pages[pages.length - 2];
				beforePage.$vm.NumSteps(2)

			},

			// 拍照
			myCliack2() {
				uni.navigateTo({
					url: './company'
				})
			},


		}
	}
</script>

